
/**
 * Enumeration of behavior states
 * @class Status
 * @memberof module:jibo/bt
 */
enum Status {
    /**
     * The behavior succeeded.
     * @name module:jibo/bt.Status.SUCCEEDED
     * @type {String}
     * @readOnly
     * @default "SUCCEEDED"
     */
    SUCCEEDED,

    /**
     * The behavior failed.
     * @name module:jibo/bt.Status.FAILED
     * @type {String}
     * @readOnly
     * @default "FAILED"
     */
    FAILED,

    /**
     * The behavior was interrupted.
     * @name module:jibo/bt.Status.INTERRUPTED
     * @type {String}
     * @readOnly
     * @default "INTERRUPTED"
     */
    INTERRUPTED,

    /**
     * The behavior is in progress and hasn't returned.
     * @name module:jibo/bt.Status.IN_PROGRESS
     * @type {String}
     * @readOnly
     * @default "IN_PROGRESS"
     */
    IN_PROGRESS,

    /**
     * The behavior is in an invalid state.
     * @name module:jibo/bt.Status.INVALID
     * @type {String}
     * @readOnly
     * @default "INVALID"
     */
    INVALID,

    /**
     * Don't update the state.
     * @name module:jibo/bt.Status.PAUSED
     * @type {String}
     * @readOnly
     * @default "PAUSED"
     */
    PAUSED,

    /**
     * Only used by decorators.
     * @name module:jibo/bt.Status.WAIT
     * @type {String}
     * @readOnly
     * @default "WAIT"
     */
    WAIT

}

export default Status;